import { createRouter, createWebHistory } from 'vue-router'
import store from '../store'

// ÎèôÏ†Å import ÎåÄÏã† Ï†ïÏ†Å import ÏÇ¨Ïö© (GitHub Pages Ìò∏Ìôò)
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import AuthCallback from '../views/AuthCallback.vue'
import NotFound from '../views/NotFound.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { 
      title: 'Ìôà - Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏',
      requiresAuth: false 
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { 
      title: 'Î°úÍ∑∏Ïù∏ - Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏',
      requiresAuth: false 
    }
  },
  {
    path: '/auth/callback',
    name: 'AuthCallback',
    component: AuthCallback,
    meta: { 
      title: 'Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë...',
      requiresAuth: false 
    }
  },
  {
    path: '/404',
    name: 'NotFound',
    component: NotFound,
    meta: { 
      title: 'ÌéòÏù¥ÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§',
      requiresAuth: false 
    }
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/404'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// ÎùºÏö∞ÌÑ∞ Í∞ÄÎìú
router.beforeEach((to, from, next) => {
  console.log('üîÑ ÎùºÏö∞ÌÑ∞ Ïù¥Îèô:', from.path, '‚Üí', to.path)
  
  // ÌéòÏù¥ÏßÄ ÌÉÄÏù¥ÌãÄ ÏÑ§Ï†ï
  document.title = to.meta.title || 'Ï£ºÏãù ÏûêÎèôÎß§Îß§ ÏÇ¨Ïù¥Ìä∏'
  
  // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú ÌéòÏù¥ÏßÄ Ï≤¥ÌÅ¨
  if (to.meta.requiresAuth) {
    const isAuthenticated = store.getters['auth/isAuthenticated']
    
    if (!isAuthenticated) {
      console.log('üîí Ïù∏Ï¶ù ÌïÑÏöî - Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô')
      next({
        name: 'Login',
        query: { redirect: to.fullPath }
      })
      return
    }
  }
  
  // Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÏóê Ï†ëÍ∑ºÌïòÎäî Í≤ΩÏö∞
  if (to.name === 'Login' && store.getters['auth/isAuthenticated']) {
    console.log('‚úÖ Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Îê® - ÌôàÏúºÎ°ú Ïù¥Îèô')
    next({ name: 'Home' })
    return
  }
  
  next()
})

export default router