import apiClient from '@/utils/api'
import { useToast } from 'vue-toastification'

const state = {
  articles: [],
  loading: false,
  searchKeyword: ''
}

const mutations = {
  SET_ARTICLES(state, articles) {
    state.articles = articles
  },
  
  SET_LOADING(state, loading) {
    state.loading = loading
  },
  
  SET_SEARCH_KEYWORD(state, keyword) {
    state.searchKeyword = keyword
  },
  
  CLEAR_ARTICLES(state) {
    state.articles = []
    state.searchKeyword = ''
  }
}

const actions = {
  async searchNews({ commit }, keyword) {
    if (!keyword || keyword.trim() === '') {
      const toast = useToast()
      toast.warning('Í≤ÄÏÉâ ÌÇ§ÏõåÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
      return
    }
    
    commit('SET_LOADING', true)
    commit('SET_SEARCH_KEYWORD', keyword)
    
    try {
      const response = await apiClient.get('/news/search', {
        params: { keyword: keyword.trim() }
      })
      
      if (response.data.success) {
        commit('SET_ARTICLES', response.data.data)
        
        if (response.data.data.length === 0) {
          const toast = useToast()
          toast.info('Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.')
        }
      } else {
        throw new Error(response.data.message || 'Îâ¥Ïä§ Í≤ÄÏÉâÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    } catch (error) {
      console.error('Îâ¥Ïä§ Í≤ÄÏÉâ Ïò§Î•ò:', error)
      commit('SET_ARTICLES', [])
      
      const toast = useToast()
      toast.error(error.response?.data?.message || 'Îâ¥Ïä§ Í≤ÄÏÉâ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.')
    } finally {
      commit('SET_LOADING', false)
    }
  },
  
  clearNews({ commit }) {
    commit('CLEAR_ARTICLES')
  }
}

const getters = {
  articles: state => state.articles,
  isLoading: state => state.loading,
  searchKeyword: state => state.searchKeyword,
  hasArticles: state => state.articles.length > 0
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}