import axios from 'axios'

// API Î≤†Ïù¥Ïä§ URL ÏÑ§Ï†ï
const API_BASE_URL = process.env.VUE_APP_API_URL || '/api'

// console.log('üåê API Base URL:', API_BASE_URL)

// Axios Ïù∏Ïä§ÌÑ¥Ïä§ ÏÉùÏÑ±
const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json'
  }
})

// ÏöîÏ≤≠ Ïù∏ÌÑ∞ÏÖâÌÑ∞ (ÌÜ†ÌÅ∞ ÏûêÎèô Ï∂îÍ∞Ä)
apiClient.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token')
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`
    }
    
    console.log(`üîç API ÏöîÏ≤≠: ${config.method?.toUpperCase()} ${config.url}`)
    return config
  },
  (error) => {
    console.error('‚ùå API ÏöîÏ≤≠ ÏóêÎü¨:', error)
    return Promise.reject(error)
  }
)

// ÏùëÎãµ Ïù∏ÌÑ∞ÏÖâÌÑ∞ (ÏóêÎü¨ Ï≤òÎ¶¨)
apiClient.interceptors.response.use(
  (response) => {
    console.log(`‚úÖ API ÏùëÎãµ: ${response.status} ${response.config.url}`)
    return response
  },
  (error) => {
    console.error('‚ùå API ÏùëÎãµ ÏóêÎü¨:', error)
    
    if (error.response?.status === 401) {
      // ÌÜ†ÌÅ∞ ÎßåÎ£å Ïãú Î°úÍ∑∏ÏïÑÏõÉ
      console.log('üîë ÌÜ†ÌÅ∞ ÎßåÎ£å - Î°úÍ∑∏ÏïÑÏõÉ Ï≤òÎ¶¨')
      localStorage.removeItem('token')
      
      // ÌòÑÏû¨ ÌéòÏù¥ÏßÄÍ∞Ä Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÍ∞Ä ÏïÑÎãå Í≤ΩÏö∞ÏóêÎßå Î¶¨Îã§Ïù¥Î†âÌä∏
      if (window.location.pathname !== '/login') {
        window.location.href = '/login'
      }
    }
    
    return Promise.reject(error)
  }
)

export default apiClient
export { API_BASE_URL }