import apiClient from '@/utils/api'
import { useToast } from 'vue-toastification'

const state = {
  user: null,
  token: localStorage.getItem('token') || null,
  isAuthenticated: false
}

const mutations = {
  SET_USER(state, user) {
    state.user = user
    state.isAuthenticated = !!user
  },
  
  SET_TOKEN(state, token) {
    state.token = token
    if (token) {
      localStorage.setItem('token', token)
      // apiClientÎäî Ïù¥ÎØ∏ Ïù∏ÌÑ∞ÏÖâÌÑ∞ÏóêÏÑú ÌÜ†ÌÅ∞ÏùÑ ÏûêÎèôÏúºÎ°ú Ï∂îÍ∞ÄÌï®
    } else {
      localStorage.removeItem('token')
    }
  },
  
  LOGOUT(state) {
    state.user = null
    state.token = null
    state.isAuthenticated = false
    localStorage.removeItem('token')
  }
}

const actions = {
  async loadUserProfile({ commit, state }) {
    try {
      if (!state.token) return
      
      console.log('üë§ ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Î°úÎî© Ï§ë...')
      const response = await apiClient.get('/auth/profile')
      
      if (response.data.success) {
        commit('SET_USER', response.data.user)
        console.log('‚úÖ ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Î°úÎìú ÏÑ±Í≥µ:', response.data.user.email)
      } else {
        // ÌÜ†ÌÅ∞Ïù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
        console.log('‚ö†Ô∏è Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÌÜ†ÌÅ∞ - Î°úÍ∑∏ÏïÑÏõÉ Ï≤òÎ¶¨')
        commit('LOGOUT')
      }
    } catch (error) {
      console.error('‚ùå ÌîÑÎ°úÌïÑ Î°úÎìú Ïã§Ìå®:', error)
      commit('LOGOUT')
    }
  },
  
  async loginWithToken({ commit, dispatch }, token) {
    try {
      console.log('üîë ÌÜ†ÌÅ∞ÏúºÎ°ú Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë...')
      commit('SET_TOKEN', token)
      await dispatch('loadUserProfile')
      
      const toast = useToast()
      toast.success('Î°úÍ∑∏Ïù∏ÎêòÏóàÏäµÎãàÎã§!')
      
      return true
    } catch (error) {
      console.error('‚ùå ÌÜ†ÌÅ∞ Î°úÍ∑∏Ïù∏ Ïã§Ìå®:', error)
      commit('LOGOUT')
      
      const toast = useToast()
      toast.error('Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      
      return false
    }
  },
  
  async logout({ commit }) {
    try {
      console.log('üëã Î°úÍ∑∏ÏïÑÏõÉ Ï≤òÎ¶¨ Ï§ë...')
      await apiClient.post('/auth/logout')
    } catch (error) {
      console.error('‚ùå Î°úÍ∑∏ÏïÑÏõÉ API Ìò∏Ï∂ú Ïã§Ìå®:', error)
    } finally {
      commit('LOGOUT')
      
      const toast = useToast()
      toast.success('Î°úÍ∑∏ÏïÑÏõÉÎêòÏóàÏäµÎãàÎã§.')
    }
  },
  
  // OAuth Î°úÍ∑∏Ïù∏ URL ÏÉùÏÑ±
  getGoogleLoginUrl() {
    const baseUrl = process.env.VUE_APP_API_URL?.replace('/api', '') || ''
    return `${baseUrl}/api/auth/google`
  },
  
  getKakaoLoginUrl() {
    const baseUrl = process.env.VUE_APP_API_URL?.replace('/api', '') || ''
    return `${baseUrl}/api/auth/kakao`
  }
}

const getters = {
  isAuthenticated: state => state.isAuthenticated,
  user: state => state.user,
  token: state => state.token,
  userName: state => state.user?.name || '',
  userAvatar: state => state.user?.avatar || ''
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}