<template>
  <div class="auth-callback">
    <div class="callback-container">
      <div class="callback-content">
        <div class="loading-spinner-large"></div>
        <h2 class="callback-title">{{ title }}</h2>
        <p class="callback-message">{{ message }}</p>
        <div v-if="showDebug" class="debug-info">
          <h4>ÎîîÎ≤ÑÍ∑∏ Ï†ïÎ≥¥:</h4>
          <pre>{{ debugInfo }}</pre>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'AuthCallback',
  data() {
    return {
      title: 'Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë...',
      message: 'Ïû†ÏãúÎßå Í∏∞Îã§Î†§Ï£ºÏÑ∏Ïöî.',
      showDebug: false,
      debugInfo: {}
    }
  },
  async mounted() {
    console.log('üé¨ AuthCallback Ïª¥Ìè¨ÎÑåÌä∏ ÎßàÏö¥Ìä∏Îê®')
    // console.log('üåê ÌòÑÏû¨ URL:', window.location.href)
    // console.log('üìç ÎùºÏö∞Ìä∏ Ï†ïÎ≥¥:', this.$route)
    
    await this.handleAuthCallback()
  },
  methods: {
    ...mapActions('auth', ['loginWithToken']),
    
    async handleAuthCallback() {
      try {
        console.log('üîÑ OAuth ÏΩúÎ∞± Ï≤òÎ¶¨ ÏãúÏûë')
        
        const params = new URLSearchParams(window.location.search)
        const token = params.get('token') || this.$route.query.token
        const provider = params.get('provider') || this.$route.query.provider
        const name = params.get('name') || this.$route.query.name
        const error = params.get('error') || this.$route.query.error
        
        this.debugInfo = {
          url: window.location.href,
          queryParams: this.$route.query,
          urlParams: Object.fromEntries(params),
          token: token ? token.substring(0, 20) + '...' : 'null',
          provider,
          name: name === 'ÎØ∏Ïó∞Îèô Í≥ÑÏ†ï' ? 'ÌïúÏõêÏÑù' : (name ? decodeURIComponent(name) : 'null'),
          error
        }
        
        // console.log('üîç Ï∂îÏ∂úÎêú ÌååÎùºÎØ∏ÌÑ∞:', this.debugInfo)
        
        // ÏóêÎü¨Í∞Ä ÏûàÎäî Í≤ΩÏö∞
        if (error) {
          throw new Error(`OAuth Ïò§Î•ò: ${error}`)
        }
        
        // ÌÜ†ÌÅ∞Ïù¥ ÏóÜÎäî Í≤ΩÏö∞
        if (!token) {
          this.showDebug = true
          throw new Error('ÌÜ†ÌÅ∞Ïù¥ ÏóÜÏäµÎãàÎã§. URL ÌååÎùºÎØ∏ÌÑ∞Î•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.')
        }
        
        // console.log('üé´ ÌÜ†ÌÅ∞ Î∞úÍ≤¨:', token.substring(0, 30) + '...')
        console.log('üîê OAuth Ï†úÍ≥µÏûê:', provider)
        console.log('üë§ ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ:', decodeURIComponent(name || ''))
        
        this.message = 'ÌÜ†ÌÅ∞ÏùÑ Ï≤òÎ¶¨ÌïòÍ≥† ÏûàÏäµÎãàÎã§...'
        
        // ÌÜ†ÌÅ∞ÏúºÎ°ú Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨
        const success = await this.loginWithToken(token)
        
        if (success) {
          console.log('‚úÖ Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ! ÌôàÏúºÎ°ú Ïù¥Îèô')
          this.title = 'Î°úÍ∑∏Ïù∏ ÏôÑÎ£å!'
          this.message = 'ÌôàÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§...'
          
          // 2Ï¥à ÌõÑ ÌôàÏúºÎ°ú Ïù¥Îèô
          setTimeout(() => {
            this.$router.replace('/')
          }, 2000)
        } else {
          throw new Error('ÌÜ†ÌÅ∞ Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
        }
        
      } catch (error) {
        console.error('‚ùå Auth callback Ïò§Î•ò:', error)
        
        this.title = 'Î°úÍ∑∏Ïù∏ Ïò§Î•ò'
        this.message = `Ïò§Î•ò: ${error.message}`
        this.showDebug = true
        
        // 5Ï¥à ÌõÑ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
        setTimeout(() => {
          this.$router.replace({
            name: 'Login',
            query: { error: 'callback_failed' }
          })
        }, 5000)
      }
    }
  }
}
</script>

